<script content="module">
  import Repl, { set, toJson } from "@sveltejs/svelte-repl/src/Repl.svelte";
  import { onMount, getContext } from "svelte";
  let repl;
  let self = this;
  onMount(() => {
    console.log("mount");
    if (repl) {
      repl.set({ components: [{ name: "App", type: "svelte" }] });
    }
    const {
      bundle,
      selected,
      handle_change,
      navigate,
      register_module_editor
    } = getContext("REPL");

    console.log("asd");
    var ws = new WebSocket("ws://localhost:8099");
    ws.onmessage = function(e) {
      let event = { detail: { value: e.data } };
      handle_change(event);
    };
  });
</script>

<div style="height:800px">
  <Repl
    embedded="true"
    bind:this={repl}
    style="height:500px"
    workersUrl="http://localhost:8099" />
</div>
